import axios from "axios";
import { MessageBox } from "element-ui";

const instance = axios.create({
  baseURL: "service/",
  timeout: 5000
});

export function call(entry, params) {
  params = JSON.stringify(params);
  return new Promise((resolve, reject) => {
    instance
      .post(entry, params)
      .then(response => {
        resolve(response.data);
      })
      .catch(response => {
        let message = response.response ? response.response.data : "";
        MessageBox({
          type: "error",
          title: response.message,
          message
        });
        reject();
      });
  });
}
