<template>
  <div class="unit-panel">
    <block-head :title="unit.label" />
    <div class="unit-panel-table">
      <el-table
        :data="tables"
        height="100%"
        :highlight-current-row="false"
        border
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        @row-dblclick="selectTable"
      >
        <el-table-column fixed type="index" label="序号" width="50" />
        <el-table-column prop="label" label="显示标题" width="160" />
        <el-table-column prop="name" label="对象名" width="160" />
        <el-table-column label="标签">
          <template slot-scope="scope" v-if="scope.row.tags">
            <el-tag v-for="tag in scope.row.tags" size="mini" :key="tag">{{ tag }}</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";

export default {
  name: "UnitPanel",
  components: { BlockHead },
  computed: {
    tables() {
      if (this.unit.children) return this.unit.children.filter(child => !child.unit);
      return [];
    }
  },
  props: {
    unit: {
      type: Object
    }
  },
  methods: {
    actionClick(action) {
      this[action]();
    },
    selectTable(table) {
      this.$emit("selectTable", table);
    }
  }
};
</script>

<style lang="scss">
.unit-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;

  .unit-panel-table {
    flex: 1;
    overflow: scroll;
  }
}
</style>
