<template>
  <div class="select-tree-head">
    <el-select v-model="selectTree" size="small">
      <el-option v-for="item in tabs" :key="item" :label="item" :value="item">
      </el-option>
    </el-select>
    <div class="select-tree-head__actions">
      <template v-for="action in actions">
        <el-button
          type="text"
          :disabled="action.disabled"
          size="small"
          class="select-tree-head__actionBtn"
          :key="action.id"
          @click="actionClick(action.id)"
        >
          {{ action.title }}
        </el-button>
      </template>
    </div>
  </div>
</template>
<script>
import { call } from "@/common/service.js";
import { mapState } from "vuex";

export default {
  name: "SelectTreeHead",
  props: {
    actions: Array
  },
  data() {
    return {
      tabs: [],
      selectTree: ""
    };
  },
  computed: {
    ...mapState(["operateName"])
  },
  watch: {
    selectTree(data) {
      this.$store.dispatch("getOperateName", data);
      this.$store.dispatch("getPresetList", data);
    }
  },
  created() {
    let _this = this;
    call("preset/types", { model: "EG" }).then(data => {
      _this.tabs = data;
      _this.selectTree = data[0];
    });
  },
  methods: {
    actionClick: function(e) {
      if (e && !e.disabled) this.$emit("action-click", e);
    }
  }
};
</script>
<style lang="scss">
.select-tree-head {
  width: 100%;
  height: 36px;
  position: absolute;
  top: 8px;
  left: 0px;
  border-bottom: 1px solid #bcbcbc;
  .el-select {
    width: 120px;
    margin-left: 10px;
  }
}
.select-tree-head__actions {
  float: right;
  align-items: center;
  padding-right: 5px;
  justify-content: flex-end;
  margin-right: 10px;
}
</style>
