<template>
  <div class="table-panel">
    <div class="table-wrapper">
      <el-table
        :data="columns"
        height="100%"
        :highlight-current-row="false"
        border
        row-key="id"
        cell-class-name="nopadding"
        header-cell-class-name="header-cell-class"
        ref="fieldsTable"
        @selection-change="selectChange"
        @clearSelection="clearSelect"
      >
        <el-table-column fixed type="selection" :resizable="false" width="38" />
        <el-table-column prop="label" label="属性" max-width="240">
          <template slot-scope="scope">
            <span>{{ scope.row.label }}</span>
            <!-- <el-tag size="mini">{{ scope.row.name }}</el-tag> -->
            <!-- <el-tag size="mini">{{ scope.row.type }}</el-tag> -->
          </template>
        </el-table-column>
      </el-table>
    </div>
    <Table-columns-data :columns="columns" :entity="entity" :localSelection="localSelection" />
  </div>
</template>

<script>
import TableColumnsData from "./TableColumnsData.vue";
import { mapActions } from "vuex";

export default {
  name: "TablePanel",
  components: {
    TableColumnsData
  },
  data() {
    return {
      localSelection: [], // 所有列中 选中的列
      columns: [], // 所有列
      curTab: "preset"
    };
  },
  props: {
    entity: Object
  },
  watch: {
    entity(newValue) {
      let _this = this;
      if (!newValue || newValue.unit) return;
      this.initData();
      this.getEntity(newValue.name).then(data => {
        data.columns.forEach(element => {
          if (!element.link) {
            _this.columns.push(element);
          }
        });
      });
    }
  },
  methods: {
    ...mapActions(["getEntity"]),
    initData() {
      this.localSelection = [];
      this.columns = [];
    },
    clearSelect() {
      console.log(11);
    },
    handleClick(tab) {
      this.curTab = tab.label;
    },
    setSelect(select) {
      select.forEach(row => {
        this.$refs.fieldsTable.toggleRowSelection(row);
      });
    },
    selectChange(item) {
      let inputSelectColData = [];
      // 按照列的顺序重新排列
      this.columns.forEach(col => {
        item.forEach(ele => {
          if (col.name === ele.name) {
            inputSelectColData.push(col);
          }
        });
      });
      this.localSelection = inputSelectColData;
    }
  }
};
</script>
