<template>
  <div class="item-tree-panel line">
    <el-input class="filter" placeholder="输入关键字进行过滤" v-model="keywords" size="middle" />
    <div class="filter-wraper">
      <el-radio v-model="radio" label="1">全部项</el-radio>
      <el-radio v-model="radio" label="2">有数据项</el-radio>
    </div>
    <div class="tree-wrapper" v-if="presetList">
      <el-tree
        :data="tree"
        :filter-node-method="filterNode"
        :expand-on-click-node="false"
        node-key="name"
        ref="tree"
        :render-content="renderNode"
        highlight-current
        show-checkbox
        @current-change="nodeChange"
        @check-change="getChecked"
      >
      </el-tree>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "ItemTreePanel",
  data() {
    return {
      keywords: "",
      aotuFilter: false,
      radio: "1"
    };
  },
  props: {
    value: Object
  },
  computed: {
    ...mapState(["tree", "operateName", "presetList", "changeTitleFlag"])
  },
  watch: {
    radio(radi) {
      if (radi == "1") {
        // 全选
        this.aotuFilter = false;
        this.$refs.tree.filter(this.keywords);
      } else {
        // 显示有数据项
        this.aotuFilter = true;
        this.filterPreset();
      }
    },
    keywords(val) {
      this.$refs.tree.filter(val);
    },
    value(val) {
      if (val) this.$nextTick(() => this.$refs.tree.setCurrentKey(val.id));
    }
  },
  methods: {
    renderNode(h, { node, data }) {
      let icon2 = "el-icon-success";
      let icon = "el-icon-document";
      let label = data.label;
      if (data.root) {
        icon = "el-icon-s-home";
        node.expanded = true;
      } else if (data.unit) {
        icon = node.expanded ? "el-icon-folder-opened" : "el-icon-folder";
      }
      if (this.presetList.length > 0 && this.presetList.indexOf(data.name) > -1) {
        // 存在数据的表
        return (
          <span>
            <i class={icon} /> &nbsp; {label} &nbsp; <i class={icon2} />
          </span>
        );
      } else {
        return (
          <span>
            <i class={icon} /> &nbsp;{label}
          </span>
        );
      }
    },
    // tree 的筛选
    filterNode(value, data) {
      if (this.aotuFilter) {
        // 在存在数据的表格中筛选显示
        if (data.name) {
          let isFilter = false;
          this.presetList.forEach(ele => {
            if (!value) {
              // 不存在搜索关键字匹配
              if (data.name == ele) {
                isFilter = true;
              }
            } else {
              // 关键字匹配 + 存在数据匹配
              if (
                data.name == ele &&
                (data.label.indexOf(value) !== -1 || (data.name && data.name.indexOf(value) != -1))
              ) {
                isFilter = true;
              }
            }
          });
          return isFilter;
        } else {
          return false;
        }
      } else {
        // 关键字匹配 + 全部
        if (!value) return true;
        return data.label.indexOf(value) !== -1 || (data.name && data.name.indexOf(value) != -1);
      }
    },
    filterPreset() {
      this.$refs.tree.filter(this.keywords);
    },
    nodeChange(data) {
      let _this = this;
      if (_this.changeTitleFlag !== "isSave") {
        // 有修改未保存
        _this
          .$confirm("有修改未提交，是否放弃修改？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
          .then(() => {
            _this.$store.dispatch("getChangeTitleFlag", "isSave");
            _this.$emit("input", data);
          });
      } else {
        _this.$emit("input", data);
      }
    },
    getChecked() {
      this.$emit("select-tree-node-list", this.$refs.tree.getCheckedKeys());
    }
  }
};
</script>

<style lang="scss">
.item-tree-panel {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  .filter-wraper {
    margin-top: 10px;
  }
  .tree-wrapper {
    border: 1px solid #eee;
    margin-top: 8px;
    flex: 1;
    overflow: scroll;
    .el-tree {
      cursor: pointer;
    }
  }
}
</style>
