<template>
  <div class="index-page">
    <select-tree-head
      :actions="treeActions"
      @action-click="actionClick"
    ></select-tree-head>
    <item-tree-panel
      v-model="selectObject"
      @select-tree-node-list="selectTreeNodeList"
    />
    <div class="panel">
      <unit-panel
        v-show="isUnit"
        :unit="selectObject"
        @selectTable="entity => (selectObject = entity)"
      />
      <table-panel v-show="!isUnit" :entity="selectObject" />
    </div>
  </div>
</template>

<script>
import { call } from "@/common/service.js";
import { mapState } from "vuex";

import ItemTreePanel from "./ItemTreePanel.vue";
import UnitPanel from "./UnitPanel.vue";
import TablePanel from "./TablePanel.vue";
import SelectTreeHead from "./SelectTreeHead.vue";

export default {
  name: "Index",
  components: { ItemTreePanel, UnitPanel, TablePanel, SelectTreeHead },
  data() {
    return {
      selectObject: { name: "", label: "" },
      treeActions: []
    };
  },
  computed: {
    ...mapState(["operateName", "selectTreeNode"]),
    isUnit() {
      return this.selectObject.unit;
    }
  },
  watch: {
    selectTreeNode() {
      this.initTreeAction();
    }
  },
  created() {
    this.initTreeAction();
  },
  methods: {
    initTreeAction() {
      this.treeActions = [
        {
          id: "exportNode",
          title: "重置数据库",
          disabled: this.selectTreeNode.length == 0
        },
        {
          id: "importNode",
          title: "从数据库导入",
          disabled: this.selectTreeNode.length == 0
        },
        {
          id: "initDatabaseNode",
          title: "初始化数据库",
          disabled: this.selectTreeNode.length == 0
        },
        { id: "downloadNode", title: "打包到本地", disabled: false }
      ];
    },
    actionClick(action) {
      this[action]();
    },
    selectTreeNodeList(treeNode) {
      // 筛选去除父节点
      let _treeNode = treeNode.filter(item => item);
      this.$store.dispatch("getSelectTreeNode", _treeNode);
    },
    // 重置数据库
    exportNode() {
      this.confirmOperate(
        "将用 " + this.operateName + "覆盖数据库对应数据表数据，确定吗？",
        "重置数据库",
        this.requestExport
      );
    },
    requestExport() {
      let _this = this;
      call("preset/exportToDb", {
        model: "EG",
        dataSet: this.operateName,
        entities: this.selectTreeNode
      }).then(() => {
        _this.$message("重置数据库成功");
      });
    },
    // 从数据库导入
    importNode() {
      this.confirmOperate(
        "将用数据库中数据覆盖 " + this.operateName + "，确定吗？",
        "从数据库导入",
        this.requestImport
      );
    },
    requestImport() {
      let _this = this;
      call("preset/exportToDb", {
        model: "EG",
        dataSet: this.operateName,
        entities: this.selectTreeNode
      }).then(() => {
        _this.$message("从数据库导入成功");
        _this.$store.dispatch("getPresetList", _this.operateName);
      });
    },
    // 打包到本地
    downloadNode() {
      // 创建 a 标签
      let a = document.createElement("a");
      a.href = "api/api/preset/download/" + this.operateName;
      a.click();
    },
    // 初始化数据库
    initDatabaseNode() {
      this.confirmOperate(
        "要重新创建数据库，并用" + this.operateName + "初始化，确定吗？",
        "初始化数据库",
        this.requestInit
      );
    },
    requestInit() {
      let _this = this;
      call("preset/initDatabase", {
        model: "EG",
        dataSet: this.operateName
      }).then(() => {
        _this.$message("初始化数据库成功");
      });
    },
    // 确认窗口
    confirmOperate(params1, params2, callback) {
      this.$confirm(params1, params2, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        callback();
      });
    }
  }
};
</script>

<style lang="scss">
.index-page {
  height: 100vh;
  width: 100vw;
  padding: 8px;
  display: flex;
  overflow: hidden;
  .item-tree {
    min-width: 280px;
  }
  .panel {
    flex: 1;
    margin-left: 8px;
    overflow: hidden;
    margin-top: 44px;
  }
}
</style>
