<template>
  <div class="table-columns-panel">
    <block-head
      :title="`${entity.label} - ${entity.name}`"
      :changeTitle="changeTitle"
      :actions="tableActions"
      @action-click="actionClick"
    />
    <div class="preset-table-wrapper" v-if="localSelection.length > 0">
      <div class="filter-wraper">
        <el-input
          placeholder="搜索关键字"
          icon="search"
          v-model="schfilter"
          class="filter-key"
          size="small"
        >
        </el-input>
      </div>
      <el-table
        :data="filterTableData"
        height="100%"
        border
        ref="inputMultiTable"
        @selection-change="inputSelectChange"
        @clearSelection="clearinputSelect"
        @cell-dblclick="dblhandleCellClick"
        @cell-click="handleCellClick"
        :row-class-name="tableRowClassName"
      >
        <el-table-column fixed type="selection" :resizable="false" width="38" />
        <div v-for="(item, i) in localSelection" :key="i">
          <el-table-column
            v-if="item.type == 'VARCHAR'"
            :prop="item.name"
            :label="item.label"
            :render-header="renderHeader"
          >
            <template slot-scope="scope">
              <div class="col-normal" v-if="scope.row.isEdit">
                <el-input
                  @change="inputChange(scope.row)"
                  v-model="scope.row[item.name]"
                  placeholder="请输入正确类型数据"
                ></el-input>
              </div>
              <div class="col-normal" v-else>
                <span>{{ scope.row[item.name] }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            v-if="item.type == 'INT' || item.type == 'SMALLINT'"
            :prop="item.name"
            :label="item.label"
            :render-header="renderHeader"
          >
            <template slot-scope="scope">
              <div class="col-normal" v-if="scope.row.isEdit">
                <!-- @input.native="inputChange" -->
                <el-input
                  @change="inputChange(scope.row)"
                  v-model="scope.row[item.name]"
                  placeholder="请输入正确类型数据"
                ></el-input>
              </div>
              <div class="col-normal" v-else>
                <span>{{ scope.row[item.name] }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            v-if="item.type == 'DATE'"
            :prop="item.name"
            :label="item.label"
            :render-header="renderHeader"
          >
            <template slot-scope="scope">
              <div class="col-date" v-if="scope.row.isEdit">
                <el-date-picker
                  v-model="scope.row[item.name]"
                  placeholder
                  @change="inputChange(scope.row)"
                  type="date"
                  value-format="yyyy-MM-dd"
                ></el-date-picker>
              </div>
              <div class="col-date" v-else>
                <span>{{ scope.row[item.name] }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            v-if="item.type == 'TIMESTAMP'"
            :prop="item.name"
            :label="item.label"
            :render-header="renderHeader"
          >
            <template slot-scope="scope">
              <div class="col-date" v-if="scope.row.isEdit">
                <el-date-picker
                  v-model="scope.row[item.name]"
                  placeholder
                  @change="inputChange(scope.row)"
                  type="datetime"
                  value-format="yyyy-MM-dd HH:mm:ss"
                ></el-date-picker>
              </div>
              <div class="col-date" v-else>
                <span>{{ scope.row[item.name] }}</span>
              </div>
            </template>
          </el-table-column>
        </div>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button
              v-if="!scope.row.isEdit"
              @click="handleEdit(scope.$index, scope.row)"
              type="text"
              size="small"
              >编辑</el-button
            >
            <el-button
              v-else
              @click="handleFinish(scope.$index, scope.row)"
              type="text"
              size="small"
              >完成</el-button
            >
            <el-button
              v-if="scope.row.isEdit"
              @click="handleCancle(scope.$index, scope.row)"
              type="text"
              size="small"
              >取消</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import BlockHead from "./BlockHead.vue";
import { call } from "@/common/service.js";
import { mapState } from "vuex";

export default {
  name: "TableColumnsData",
  components: { BlockHead },
  props: ["localSelection", "columns", "entity"], // 选中列， 所有列， 选中树节点数据
  data() {
    return {
      inputTableData: [], // 编辑的表格数据
      inputSelection: [], // 选中的值
      selectionItemIndexes: [], // 选中的索引
      changeTitle: "无修改",
      beforeEditData: [],
      schfilter: "",
      editRowIndex: 0 // 当前编辑行
    };
  },
  computed: {
    ...mapState(["operateName", "changeTitleFlag"]),
    filterTableData() {
      console.log(this.localSelection);
      if (this.schfilter == "") return this.inputTableData;
      let filterD = this.inputTableData.filter(this.filterKey);
      return filterD;
    },
    tableActions() {
      return [
        // { id: "cancelWorker", title: "撤销", disabled: this.beforeEditData.length > 1 },
        {
          id: "delRow",
          title: "删除行",
          icon: "el-icon-delete",
          disabled: this.inputSelection.length == 0
        },
        { id: "addRow", title: "添加行", icon: "el-icon-circle-plus" },
        { id: "postSave", title: "保存", icon: "el-icon-upload" }
      ];
    }
  },
  watch: {
    changeTitleFlag(val) {
      if (val !== "isSave") {
        this.changeTitle = "有未保存的修改";
      } else {
        this.changeTitle = "无修改";
      }
    },
    operateName() {
      // 先初始化
      this.initData();
      // 重新load数据
      this.requestLoad(this.columns);
    },
    columns(newValue) {
      if (newValue.length == 0) return;
      this.initData();
      // 获取表格下已有数据列表
      this.requestLoad(newValue);
    }
  },
  methods: {
    filterKey(item) {
      let _this = this;
      let filterResult = false;
      for (let key in item) {
        if (item[key].toString().indexOf(_this.schfilter) != -1) {
          filterResult = true;
        }
      }
      return filterResult;
    },
    renderHeader(h, { column, $index }) {
      let l =
        column.label.length + this.localSelection[$index - 1].type.length + 2;
      let f = 14;
      column.minWidth = f * l;
      return [
        h("span", {}, [column.label]),
        h(
          "el-tag",
          {
            size: "mini",
            style:
              "font-size:12px;height: 20px;padding: 0 5px;line-height: 19px;margin-left:5px;"
          },
          [this.localSelection[$index - 1].type]
        )
      ];
    },
    requestLoad(val) {
      let _this = this;
      call("preset/load", {
        model: "EG",
        dataSet: this.operateName,
        entityName: this.entity.name
      }).then(data => {
        if (data.length === 0) {
          // 根据columns 创建初始数据
          _this.initTableData();
        } else {
          data.forEach(item => {
            item.isEdit = false;
            item.isSave = true;
          });
          _this.inputTableData = data;
        }
        // 根据data 重新设置 localSelection
        let inputSelectColData = [];
        val.forEach(item => {
          for (let key in _this.inputTableData[0]) {
            if (item.name === key) {
              inputSelectColData.push(item);
            }
          }
        });
        this.beforeEditData.push(_this.inputTableData);
        // qingkong
        _this.$parent.$refs.fieldsTable.clearSelection();
        _this.$parent.setSelect(inputSelectColData);
      });
    },
    initTableData() {
      // 数据为空，默认选中所有列，并且创建 一行空数据
      this.inputTableData = [];
      this.schfilter = "";
      this.beforeEditData = [];
      let newItem = {};
      this.columns.forEach(item => {
        newItem[item.name] = "";
      });
      newItem.isEdit = false;
      newItem.isSave = true;
      this.inputTableData.push(newItem);
      // 修改记录
      // this.beforeEditData = JSON.parse(JSON.stringify(this.inputTableData));
      // this.$store.dispatch("getChangeTitleFlag", "isSave");
    },
    actionClick(action) {
      this[action]();
    },
    initData() {
      // this.inputTableData = [];
      this.schfilter = "";
      this.inputSelection = [];
      this.beforeEditData = [];
    },
    // 添加一行
    addRow() {
      if (this.localSelection.length == 0) return;
      // 修改记录
      this.beforeEditData = JSON.parse(JSON.stringify(this.inputTableData));
      let _this = this;
      let dataAbj = {};
      this.localSelection.forEach(ele => {
        dataAbj[ele.name] = "";
        dataAbj.isEdit = true; // 当前行可编辑状态， true表示可编辑
        dataAbj.isSave = true; // 当前行的修改状态， true 表示无修改
        _this.inputTableData.forEach(item => {
          // 初始化可编辑状态
          let ob = {};
          ob.isEdit = false;
          if (!item[ele.name]) {
            ob[ele.name] = "";
          }
          item = Object.assign({}, item, ob);
        });
      });
      this.inputTableData.unshift(dataAbj);
      // 修改记录
      this.$store.dispatch("getChangeTitleFlag", "add");
    },
    delRow() {
      if (this.selectionItemIndexes.length == 0) return;
      // 修改记录
      this.beforeEditData = JSON.parse(JSON.stringify(this.inputTableData));
      let _this = this;
      this.selectionItemIndexes.forEach(item => {
        _this.inputTableData.splice(item, 1, null);
      });
      // 将值为null的过滤掉
      this.inputTableData = this.inputTableData.filter(item => item);
      this.$refs.inputMultiTable.clearSelection();
      // 修改记录
      this.$store.dispatch("getChangeTitleFlag", "delete");
    },
    clearinputSelect() {
      console.log(22);
    },
    postSave() {
      let _this = this;
      if (this.localSelection.length == 0) return;
      let tableData = JSON.parse(JSON.stringify(this.inputTableData));
      let postData = [];
      // 筛选出 tableData 中的某些字段
      tableData.forEach(item => {
        // 每一行的数据
        let newItem = {};
        for (const key in item) {
          _this.localSelection.forEach(field => {
            if (field.name === key) {
              newItem[key] = item[key];
            }
          });
        }
        postData.push(newItem);
      });
      call("preset/save", {
        model: "EG",
        presetType: this.operateName,
        entityName: this.entity.name,
        data: postData
      }).then(data => {
        if (data) {
          _this.$message("数据上传成功");
          _this.inputTableData.forEach(item => {
            item.isEdit = false;
            item.isSave = true;
          });
          _this.$store.dispatch("getPresetList", _this.operateName);
          _this.$store.dispatch("getChangeTitleFlag", "isSave");
          // 修改记录
          this.beforeEditData = [_this.inputTableData];
        }
      });
    },
    inputSelectChange(rows) {
      this.inputSelection = rows;
      //用一个变量来存放被选中的index
      this.selectionItemIndexes = [];
      rows.forEach(item => {
        this.selectionItemIndexes.push(item.index);
      });
    },
    tableRowClassName(row) {
      //设置row对象的index
      row.row.index = row.rowIndex;
    },
    handleEdit(index, row) {
      this.beforeEditData.push(JSON.parse(JSON.stringify(this.inputTableData)));
      // 将为true 的设置为false
      this.inputTableData.forEach(item => {
        item.isEdit = false;
      });
      row.isEdit = true;
      this.editRowIndex = index;
    },
    handleFinish(index, row) {
      row.isEdit = false;
      row.isSave = true; // 保存当前行修改
    },
    handleCancle(index, row) {
      let _this = this;
      if (_this.changeTitleFlag !== "isSave" && row.isSave == false) {
        this.$confirm("确定放弃所做的修改么", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          _this.inputTableData = JSON.parse(
            JSON.stringify(_this.beforeEditData.pop())
          );
          if (_this.beforeEditData.length == 1) {
            _this.$store.dispatch("getChangeTitleFlag", "isSave");
          }
          row.isSave = true;
          row.isEdit = false;
        });
      } else {
        row.isEdit = false;
      }
    },
    dblhandleCellClick(row) {
      this.beforeEditData.push(JSON.parse(JSON.stringify(this.inputTableData)));
      // 将为 true 的设置为 false
      this.inputTableData.forEach(item => {
        item.isEdit = false;
        item.isSave = true;
      });
      row.isEdit = true;
      this.editRowIndex = row.index;
    },
    handleCellClick(row) {
      if (row.index === this.editRowIndex) return;
      this.inputTableData.forEach(item => {
        item.isEdit = false;
        item.isSave = true;
      });
    },
    inputChange(row) {
      // this.beforeEditData.push(JSON.parse(JSON.stringify(this.inputTableData)));
      this.$store.dispatch("getChangeTitleFlag", "edit");
      row.isSave = false; // 表示未保存
      this.$forceUpdate();
    }
  }
};
</script>
<style lang="scss">
.table-columns-panel {
  .block-head {
    position: absolute;
    top: 0px;
    width: calc(100%);
  }
}
</style>

<style lang="scss" scoped>
.table-columns-panel {
  .filter-key {
    width: 200px;
  }
  .preset-table-wrapper {
    position: absolute;
    top: 40px;
    width: calc(100% - 200px);
    height: calc(100% - 40px);
    left: 200px;
    .col-date .el-date-editor.el-input {
      width: 139px;
    }
    .el-table {
      height: calc(100% - 40px) !important;
      margin-top: 8px;
    }
  }
}
</style>
