import Vue from "vue";
import Vuex from "vuex";
import { call } from "@/common/service.js";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    tree: [],
    entities: {},
    operateName: "",
    presetList: null,
    selectTreeNode: [],
    changeTitleFlag: "isSave"
  },
  mutations: {
    setTree(state, tree) {
      tree.root = true;
      state.tree = [tree];
    },
    setEntity(state, payload) {
      state.entities[payload.name] = payload.data;
    },
    setOperateName(state, preset) {
      state.operateName = preset;
    },
    setPresetList(state, data) {
      state.presetList = data;
    },
    setSelectTreeNode(state, data) {
      state.selectTreeNode = data;
    },
    setChangeTitleFlag(state, data) {
      state.changeTitleFlag = data;
    }
  },
  actions: {
    loadTree({ commit }) {
      call("data/dataTree", { model: "EG" }).then(data =>
        commit("setTree", data)
      );
    },
    getEntity({ commit, state }, name) {
      let entityInfo = state.entities[name];
      if (entityInfo == null) {
        return call("data/entity", { model: "EG", name }).then(data => {
          commit("setEntity", { name, data });
          return data;
        });
      } else return entityInfo;
    },
    getOperateName(context, data) {
      context.commit("setOperateName", data);
    },
    getPresetList(context, preset) {
      call("preset/hasPreset", { model: "EG", dataSet: preset }).then(data => {
        let newList = [];
        data.forEach(element => {
          newList.push(element.split(".")[0]);
        });
        context.commit("setPresetList", newList);
      });
    },
    getSelectTreeNode(context, data) {
      context.commit("setSelectTreeNode", data);
    },
    getChangeTitleFlag(context, data) {
      context.commit("setChangeTitleFlag", data);
    }
  }
});
